// stylelint-disable no-duplicate-selectors, selector-no-qualifying-type

//
// Grid examples
//

.bd-example-row {
	.row {
		> .col,
		> [class^="col-"] {
			padding-top: .75rem;
			padding-bottom: .75rem;
			background-color: rgba(86, 61, 124, .15);
			border: 1px solid rgba(86, 61, 124, .2);
		}
	}

	.row + .row {
		margin-top: 1rem;
	}

	.flex-items-top,
	.flex-items-middle,
	.flex-items-bottom {
		min-height: 6rem;
		background-color: rgba(255, 0, 0, .1);
	}
}

.bd-example-row-flex-cols .row {
	min-height: 10rem;
	background-color: rgba(255, 0, 0, .1);
}

.bd-highlight {
	background-color: rgba($bd-purple, .15);
	border: 1px solid rgba($bd-purple, .15);
}

.bd-example-responsive-containers {
	[class^="container"] {
		padding-top: .75rem;
		padding-bottom: .75rem;
		background-color: rgba(86, 61, 124, .15);
		border: 1px solid rgba(86, 61, 124, .2);
	}
}

// Grid mixins
.example-container {
	width: 800px;
	@include make-container();
}

.example-row {
	@include make-row();
}

.example-content-main {
	@include make-col-ready();

	@include media-breakpoint-up(sm) {
		@include make-col(6);
	}

	@include media-breakpoint-up(lg) {
		@include make-col(8);
	}
}

.example-content-secondary {
	@include make-col-ready();

	@include media-breakpoint-up(sm) {
		@include make-col(6);
	}

	@include media-breakpoint-up(lg) {
		@include make-col(4);
	}
}


//
// Docs examples
//
.bd-example-wrapper {
	margin-top: 1rem;
	margin-bottom: 1.5rem;

	.bd-example {
		padding: 0;
		border: 0;
	}

	@include media-breakpoint-up(lg) {
		.w-1920 {
			width: 1920px !important;
		}
		.w-1200 {
			width: 1200px !important;
		}
		.w-1170 {
			width: 1170px !important;
		}
	}
}

.bd-example {
	position: relative;
	padding: 20px;
	border: solid 3px #f8f9fa;
	font-size: $font-size-base;

	@include clearfix();

	+ .highlight,
	+ .clipboard + .highlight {
		margin-top: 0;
	}

	+ p {
		margin-top: 2rem;
	}

	.custom-file-input:lang(es) ~ .custom-file-label::after {
		content: "Elegir";
	}

	> .form-control {
		+ .form-control {
			margin-top: .5rem;
		}
	}

	> .nav + .nav,
	> .alert + .alert,
	> .navbar + .navbar,
	> .progress + .progress,
	> .progress + .btn {
		margin-top: 1rem;
	}

	> .dropdown-menu:first-child {
		position: static;
		display: block;
	}

	> .form-group:last-child {
		margin-bottom: 0;
	}

	> .close {
		float: none;
	}
}

// Typography
.bd-example-type {
	.table {
		td {
			padding: 1rem 0;
			border-color: #eee;
		}

		tr:first-child td {
			border-top: 0;
		}
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-top: 0;
		margin-bottom: 0;
	}
}

// Contextual background colors
.bd-example-bg-classes p {
	padding: 1rem;
}

// Images
.bd-example {
	> svg + svg,
	> img + img {
		margin-left: .5rem;
	}
}

// Buttons
.bd-example {
	> .btn,
	> .btn-group {
		margin-top: .25rem;
		margin-bottom: .25rem;
	}

	> .btn-toolbar + .btn-toolbar {
		margin-top: .5rem;
	}
}

// Forms
.bd-example-control-sizing select,
.bd-example-control-sizing input[type="text"] + input[type="text"] {
	margin-top: .5rem;
}

.bd-example-form .input-group {
	margin-bottom: .5rem;
}

.bd-example > textarea.form-control {
	resize: vertical;
}

// List groups
.bd-example > .list-group {
	max-width: 400px;
}

.bd-example > [class*="list-group-horizontal"] {
	max-width: 100%;
}

// Navbars
.bd-example {
	.fixed-top,
	.sticky-top {
		position: static;
		margin: -1rem -1rem 1rem;
	}

	.fixed-bottom {
		position: static;
		margin: 1rem -1rem -1rem;
	}

	@include media-breakpoint-up(sm) {
		.fixed-top,
		.sticky-top {
			margin: -1.5rem -1.5rem 1rem;
		}
		.fixed-bottom {
			margin: 1rem -1.5rem -1.5rem;
		}
	}
}

// Pagination
.bd-example .pagination {
	margin-top: .5rem;
	margin-bottom: .5rem;
}

// Example modals
.modal {
	z-index: 1072;

	.tooltip,
	.popover {
		z-index: 1073;
	}
}

.modal-backdrop {
	z-index: 1071;
}

.bd-example-modal {
	background-color: #fafafa;

	.modal {
		position: relative;
		top: auto;
		right: auto;
		bottom: auto;
		left: auto;
		z-index: 1;
		display: block;
	}

	.modal-dialog {
		left: auto;
		margin-right: auto;
		margin-left: auto;
	}
}

// Popovers
.bd-example-popover-static {
	padding-bottom: 1.5rem;
	background-color: #f9f9f9;

	.popover {
		position: relative;
		display: block;
		float: left;
		width: 260px;
		margin: 1.25rem;
	}
}

// Tooltips
.tooltip-demo a {
	white-space: nowrap;
}

.bd-example-tooltip-static .tooltip {
	position: relative;
	display: inline-block;
	margin: 10px 20px;
	opacity: 1;
}

// Scrollspy demo on fixed height div
.scrollspy-example {
	position: relative;
	height: 200px;
	margin-top: .5rem;
	overflow: auto;
}

.scrollspy-example-2 {
	position: relative;
	height: 350px;
	overflow: auto;
}

.bd-example-border-utils {
	[class^="border"] {
		display: inline-block;
		width: 5rem;
		height: 5rem;
		margin: .25rem;
		background-color: #f5f5f5;
	}
}

.bd-example-border-utils-0 {
	[class^="border"] {
		border: 1px solid $border-color;
	}
}

.bd-example-forms-input-group-workaround .fix-rounded-right {
	@include border-right-radius(.2rem !important);
}

//
// Code snippets
//

.highlight {
	padding: 1rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	background-color: $gray-100;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	max-height: 400px;
	overflow: auto;

	@include media-breakpoint-up(sm) {
		padding: 1.5rem;
	}
}

.bd-content .highlight {
	margin-right: (-$grid-gutter-width / 2);
	margin-left: (-$grid-gutter-width / 2);

	@include media-breakpoint-up(sm) {
		margin-right: 0;
		margin-left: 0;
	}
}

.highlight {
	pre {
		padding: 0;
		margin-top: 0;
		margin-bottom: 0;
		background-color: transparent;
		border: 0;
	}

	pre code {
		@include font-size(inherit);
		color: $gray-900; // Effectively the base text color
	}
}
