// stylelint-disable no-duplicate-selectors, selector-max-combinators, selector-max-compound-selectors, selector-max-type, selector-no-qualifying-type

//
// Automatically style Markdown-based tables like a Bootstrap `.table`.
//

.bd-content {
	order: 1;
	padding-top: 70px;
	@include media-breakpoint-up(md) {
		padding-left: 80px;
	}
	@supports (position: sticky) {
		padding-top: 0;
		@include media-breakpoint-up(md) {
			padding-left: 50px;
		}
	}

	// Hack the sticky header
	> h2[id],
	> h3[id],
	> h4[id] {
		pointer-events: none;

		&::before {
			display: block;
			height: 6rem;
			margin-top: -6rem;
			content: "";
		}
	}

	> table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 1rem;

		@include media-breakpoint-down(md) {
			display: block;
			overflow-x: auto;

			&.table-bordered {
				border: 0;
			}
		}

		// Cells
		> thead,
		> tbody,
		> tfoot {
			> tr {
				> th,
				> td {
					padding: $table-cell-padding;
					vertical-align: top;
					border: 1px solid $table-border-color;

					> p:last-child {
						margin-bottom: 0;
					}
				}
			}
		}

		// Prevent breaking of code (e.g., Grunt tasks list)
		td:first-child > code {
			white-space: nowrap;
		}
	}
}

.bd-content-title {
	display: block;
	pointer-events: auto;
	color: $heading-color;
}

//
// Docs sections
//

.bd-content {
	> h2 {
		@include font-size($h2-font-size);
	}

	> h3 {
		@include font-size($h3-font-size);
	}

	> h4 {
		@include font-size($h4-font-size);
	}

	> h2:not(:first-child) {
		margin-top: 3rem;
	}

	> h3 {
		margin-top: 1.5rem;
	}

	> ul li,
	> ol li {
		margin-bottom: .25rem;
	}

	@include media-breakpoint-up(lg) {
		> ul,
		> ol,
		> p {
			max-width: 80%;
		}
	}
}

.bd-title {
	margin-top: 1rem;
	margin-bottom: .5rem;
	color: $heading-color;
	@include font-size(3rem);
}

.bd-lead {
	@include font-size(1.2rem);
	font-weight: 300;

	@include media-breakpoint-up(lg) {
		max-width: 80%;
	}
}

.bd-text-purple {
	color: $bd-purple;
}

.bd-text-purple-bright {
	color: $bd-purple-bright;
}

.bd-bg-purple-bright {
	background-color: $bd-purple-bright;
}
